<template>
  <v-row align="center" justify="center">
    <div id="qrcode" class="ma-12"></div>
  </v-row>
</template>

<script>
import QRCode from "qrcodejs2";

export default {
  name: "QRCodePage",

  props: ["text"],

  data: () => ({}),

  methods: {
    createQrCode() {
      new QRCode("qrcode", {
        width: 210,
        height: 210,
        text: this.text,
        colorDark: "#000000",
        colorLight: "#ffffff",
        correctLevel: QRCode.CorrectLevel.H,
      });
    },
  },

  mounted: function () {
    this.createQrCode();
  },
};
</script>
